@import '../mixins/mixins';
* {
 }  // outline: 1px solid red
body {
  background: #5B7EE2;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 15px;
  font-weight: 500;
  .red {
    color: #c30d23; }
  .yellow {
    color: #f8e72b; }
  h3 {
    font-weight: 700;
    font-size: 1em;
    text-align: center; }
  h2 {
    height: 6vw;
    width: auto;
    max-width: 600px;
    @include margin(0 auto);
    text-align: center;
    img {
      width: auto;
      height: auto;
      max-height: 100%;
      max-width: 100%; } } }

.container {
  width: 100%;
  .wrapper {
    width: 80%;
    max-width: 1200px;
    margin: 0 auto; } }

.header {
  background: url('../../images/kv_bg.jpg') no-repeat top center;
  background-size: 100% auto;
  &-body {
    position: relative; }
  &-footer {
    height: 8vw;
    background: url('../../images/kv_floor.png') repeat-x top center;
    background-size: auto 100%; } }

.castle {
  position: relative;
  z-index: 1; }

.fireworks {
  position: absolute;
  overflow: hidden;
  @include halign;
  top: 0;
  width: 100%;
  z-index: 0;
  @include flex(space-between);
  img {
    width: calc(100% / 3);
    opacity: 0;
    animation: fadein .5s forwards;
    &:nth-child(1) {
      margin-top: 10%;
      transform: scale(.9); }
    &:nth-child(2) {
      margin-top: -18%; }
    &:nth-child(3) {
      transform: scale(.9);
      animation-delay: 1.5s; } } }

.acts {
  position: absolute;
  @include halign;
  bottom: 0;
  width: 50%;
  z-index: 2;
  @include flex(flex-end, baseline);
  &-start {
    @include relative(0);
    @include flex(flex-end, baseline);
    .act:nth-child(2) {
      z-index: -1; }
    .chat {
      width: 20vw; } }
  &-end {
    @include relative(0);
    @include flex(flex-end, baseline);
    padding-left: 11.6vw;
    .chat {
      width: 25vw;
      left: 52%; } }
  .chat {
    position: absolute;
    bottom: 100%;
    transform-origin: center bottom;
    animation: shake 0.8s infinite steps(2); }
  .act {
    width: auto;
    height: 10vw;
    transform-origin: center bottom;
    animation: shake 0.8s infinite steps(2);
    & + .act {
      margin-left: -0.8vw; } } }

.tabs {
  @include relative(0);
  .sticky_icon {
    @include absolute(bottom 0 right 5vw);
    width: 15%;
    max-width: 120px;
    display: none; }
  &-head {
    @include padding(3.5% 0 3.2%);
    background: url('../../images/tabs_bg.png') repeat-x top center;
    background-size: auto 100%;
    .wrapper {
      @include flex; }
    a {
      width: 30vw;
      max-width: 300px;
      background: url('../../images/icon2.png') no-repeat center center;
      background-size: 100% auto;
      @include margin(0 2vw);
      @include relative(0);
      &:hover, &.active {
        background: url('../../images/icon.png') no-repeat center center;
        background-size: 100% auto; } }
    .sale {
      @include absolute(top -25% left -10%);
      width: 30%; } }
  &-links p {
    @include relative(0);
    @include margin(bottom 3%);
    a {
      @include valign;
      right: 0;
      width: 20%;
      margin: 0 2.5%; }
    &.tag::before {
      content: '';
      display: block;
      width: 13%;
      height: 100%;
      background: url('../../images/ico_tag.png') no-repeat top left;
      background-size: 100% auto;
      @include absolute(left -0.9% top -2.6%); } }
  &-body {
    width: calc(100% - 10vw);
    @include padding(5vw);
    @include margin(0 auto); }
  &-cont {
    display: none;
    width: 100%;
    max-width: 900px;
    @include margin(0 auto);
    @include border-radius(6vw);
    border: 1px solid #000;
    background: #002B8A;
    overflow: hidden;
    @include relative(0);
    &.active {
      display: block; }
    .wrapper {
      width: calc(100% - (5px * 2));
      border: 5px solid #002B8A;
      background: #75A2D8;
      @include border-radius(6vw);
      overflow: hidden; }
    .container {
      width: calc(100% - 6vw);
      @include padding(3vw); } }
  &-replace {
    .tabs-cont {
      max-width: 640px; }
    .container {
      width: 100%;
      @include padding(0); } } }

.info {
  font-size: 1em;
  color: #fff;
  @include padding(2vw 0);
  li {
    @include flex(flex-start, start);
    @include margin(bottom 5px);
    text-shadow: 0 2px 2px rgba(black, .8);
    span {
      display: inline-block;
      vertical-align: top; }
    a {
      display: inline-block;
      color: #0000FF;
      font-weight: 600;
      text-shadow: 0 0 0 transparent; } }
  .title {
    text-align: center;
    white-space: nowrap; }
  .cont {
    padding: 0 0 0 5px; }
  .note {
    display: block;
    p:not(:first-child) {
      @include relative(0);
      @include padding(left 1.5em);
      @include margin(top 5px);
      &::before {
        content: '●';
        display: block;
        @include absolute(left 0); } } } }

#note {
  background: #FFF;
  padding: 5% 0;
  ul {
    font-size: 1em;
    font-weight: 400;
    & + ul {
      margin-top: 5%; } }
  .dot, .star {
    position: relative;
    padding: 0 0 0 15px;
    &::before {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      position: absolute;
      left: 0;
      top: 0.4em; } }
  .dot {
    &::before {
      background: #000;
      border-radius: 50%; } }
  .star::before {
    background: url('../../images/icon-star.png') no-repeat 0 0;
    background-size: auto 100%; }
  .red {
    display: inline;
    margin: 0 2px;
    color: red; }
  .bold {
    font-weight: 700; }
  .arr {
    display: inline-block;
    width: 14px;
    vertical-align: middle;
    margin: 0 0.2em 0.2em; }
  .logo {
    width: 160px;
    display: inline-block;
    vertical-align: top; }
  a {
    display: inline-block;
    margin: 0 0.2em;
 } }    // font-size: 1em

.copyright {
  text-align: center;
  color: #b5b5b6;
  padding: 20px 0;
  font-weight: 400;
  background: #231815; }

@keyframes shake {
  0% {
    transform: scale(1, 1); }
  100% {
    transform: scale(1, 1.03); } }
